/* Basic setup */

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
}
html{
  font-size: 62.5%;
  
  
}
:root {
  --theme_color: #335eea;
  --heding_color1:#161c2d;
  --heading_color2:#335eea;
  --text_color:#869ab8;

}
p{
  font-size: 1.6rem!important;
  color: #869ab8 !important;
}
a{
  font-size: 1.6rem!important;
  text-decoration: none;
  color: #506690!important;
  font-weight: 500;

}
/* reusable component */
.btn_main{
  background: var(--theme_color);
  color: #fff!important;
  font-size: 1.8rem !important;
  font-weight: 600;
  padding: 2rem 2.5rem;
  border-radius: 0.5rem;
  transition: 0.2s ease-in-out;
  border:0.2rem solid var(--theme_color);
  
  
}
.btn_main:hover{
  background: #fff;
  color: var(--theme_color);
  border:0.2rem solid var(--theme_color);
  color: var(--theme_color)!important;
}
/* styling navbar section */
.partial_logo{
  color: var(--theme_color);
  font-size: 2.4rem;
  font-weight: bolder;

}
.main_logo {
  width: 3rem;
  height: 3rem;
}
.navbar-brand{
  padding-top: 3rem!important;
}
.nav-link{
  font-weight: 600;
  margin-left: 2rem;
  padding-top: 3rem;
}
.nav-link:hover{
  color: var(--theme_color)!important;
}
/* start header section */
#header{
  width: 100%;
  height: 80vh;
  margin-top: 10rem;
}
.passionate h1{
  font-size: 3.5rem;
  font-weight: 900;
  color:var(--heading_color2);
}
.passionate h2{
  font-size: 2.0rem;
  color: ;
  padding-bottom: 5rem!important;
  text-transform: capitalize;
}
.skill_highlight{
  color: var(--theme_color);
}
#header .hero_animate{
  animation: up_down 3s ease-in-out infinite  alternate-reverse both;
  transition: smooth;
}

@keyframes up_down{
  0%{
    transform: translateY(1.2rem);
  }
  100%{
    transform: translateY(-1.2rem);
  }
}
@-webkit-keyframes up_down{
  0%{
    transform: translateY(1.2rem);
  }
  100%{
    transform: translateY(-1.2rem);
  }
}
/* beautifull flowery effect css */

